type RouteParams = {
  id: string;
};

const PortfolioItem: React.FC = () => {
  const { id } = useParams<RouteParams>();
  const portfolio = id ? portfolioData[id as keyof typeof portfolioData] : null;

  if (!portfolio) {
    return (
      <div style={{ 
        display: 'flex', 
        flexDirection: 'column', 
        alignItems: 'center', 
        justifyContent: 'center',
        minHeight: '100vh',
        padding: '2rem' 
      }}>
        <h1>   </h1>
        <Link to="/" style={{
          display: 'inline-block',
          marginTop: '2rem',
          padding: '0.75rem 2rem',
          backgroundColor: 'var(--primary-color)',
          color: '#fff',
          borderRadius: '8px',
          textDecoration: 'none',
          fontWeight: 600
        }}>
           
        </Link>
      </div>
    );
  }

  return (
    <div style={{ marginTop: '5rem', paddingBottom: '5rem' }}>
      {/*   */}
      <motion.div 
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.8 }}
        style={{
          backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(${portfolio.image})`,
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          padding: '8rem 2rem',
          color: 'white',
          textAlign: 'center',
          marginBottom: '4rem'
        }}
      >
        <div className="container" style={{ maxWidth: '1200px', margin: '0 auto' }}>
          <motion.h1 
            initial={{ y: 30, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            style={{ 
              fontSize: '3rem', 
              fontWeight: 800, 
              marginBottom: '1.5rem' 
            }}
          >
            {portfolio.title}
          </motion.h1>
          <motion.p 
            initial={{ y: 30, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.5, delay: 0.4 }}
            style={{
              fontSize: '1.25rem',
              maxWidth: '800px',
              margin: '0 auto',
              lineHeight: 1.8
            }}
          >
            {portfolio.description}
          </motion.p>
        </div>
      </motion.div>

      {/*   */}
      <div className="container" style={{ maxWidth: '1200px', margin: '0 auto', padding: '0 2rem' }}>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '5rem' }}>
          {/*   */}
          <motion.section
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
          >
            <h2 style={{ 
              fontSize: '2.2rem', 
              fontWeight: 700, 
              marginBottom: '2rem',
              color: portfolio.color
            }}>
               
            </h2>
            <p style={{
              fontSize: '1.1rem',
              lineHeight: 1.8,
              color: '#555',
              marginBottom: '2rem'
            }}>
              {portfolio.detailDescription}
            </p>
            <div style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
              gap: '2rem',
              marginTop: '3rem'
            }}>
              {portfolio.features.map((feature, index) => (
                <motion.div
                  key={index}
                  initial={{ opacity: 0, y: 20 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  viewport={{ once: true }}
                  transition={{ duration: 0.4, delay: index * 0.1 }}
                  style={{
                    padding: '1.5rem',
                    background: '#f8f9fa',
                    borderRadius: '12px',
                    boxShadow: '0 4px 12px rgba(0, 0, 0, 0.05)'
                  }}
                >
                  <div style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '1rem'
                  }}>
                    <div style={{
                      width: '40px',
                      height: '40px',
                      borderRadius: '50%',
                      background: portfolio.color,
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      color: 'white',
                      fontWeight: 'bold'
                    }}>
                      {index + 1}
                    </div>
                    <h3 style={{ fontWeight: 600, color: '#333' }}>{feature}</h3>
                  </div>
                </motion.div>
              ))}
            </div>
          </motion.section>

          {/*   */}
          <motion.section
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
          >
            <h2 style={{ 
              fontSize: '2.2rem', 
              fontWeight: 700, 
              marginBottom: '2rem',
              color: portfolio.color
            }}>
               
            </h2>
            <div style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(auto-fill, minmax(350px, 1fr))',
              gap: '2rem'
            }}>
              {portfolio.caseStudies.map((study, index) => (
                <motion.div
                  key={index}
                  initial={{ opacity: 0, y: 20 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  viewport={{ once: true }}
                  transition={{ duration: 0.5, delay: index * 0.2 }}
                  style={{
                    padding: '2rem',
                    background: 'white',
                    borderRadius: '12px',
                    boxShadow: '0 10px 30px rgba(0, 0, 0, 0.08)',
                    border: `1px solid ${portfolio.color}20`
                  }}
                >
                  <h3 style={{ 
                    fontWeight: 700,
                    fontSize: '1.3rem',
                    marginBottom: '1rem',
                    color: '#333'
                  }}>
                    {study.title}
                  </h3>
                  <div style={{
                    height: '2px',
                    width: '50px',
                    background: portfolio.color,
                    marginBottom: '1.5rem'
                  }}></div>
                  <p style={{
                    fontSize: '1.05rem',
                    color: '#555',
                    lineHeight: 1.7
                  }}>
                    {study.result}
                  </p>
                </motion.div>
              ))}
            </div>
          </motion.section>

          {/* CTA */}
          <motion.section
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
            style={{
              padding: '3rem',
              background: `linear-gradient(to right, ${portfolio.color}, ${portfolio.color}DD)`,
              borderRadius: '16px',
              color: 'white',
              textAlign: 'center'
            }}
          >
            <h2 style={{ fontSize: '2rem', fontWeight: 700, marginBottom: '1.5rem' }}>
                
            </h2>
            <p style={{ fontSize: '1.1rem', marginBottom: '2rem', maxWidth: '700px', margin: '0 auto 2rem' }}>
                      .
            </p>
            <Link to="/contact" style={{
              display: 'inline-block',
              padding: '1rem 2.5rem',
              background: 'white',
              color: portfolio.color,
              borderRadius: '50px',
              fontWeight: 700,
              textDecoration: 'none',
              fontSize: '1.1rem',
              boxShadow: '0 10px 30px rgba(0, 0, 0, 0.15)'
            }}>
                
            </Link>
          </motion.section>
        </div>
      </div>
    </div>
  );
};

export default PortfolioItem; 